	module.exports = {"data":{"markdownRemark":{"html":"<h2>Date.prototype.getDate</h2>\n<p>The <code>getDate()</code> method returns the day of the month for the specified date according to local time.</p>\n<h3>Syntax</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code>dateObject<span class=\"token punctuation\">.</span><span class=\"token function\">getDate</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<h3>Example</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code><span class=\"token keyword\">var</span> Xmas95 <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">Date</span><span class=\"token punctuation\">(</span><span class=\"token string\">'December 25, 1995 23:15:30'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">var</span> day <span class=\"token operator\">=</span> Xmas95<span class=\"token punctuation\">.</span><span class=\"token function\">getDate</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nconsole<span class=\"token punctuation\">.</span><span class=\"token function\">log</span><span class=\"token punctuation\">(</span>day<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span> <span class=\"token comment\" spellcheck=\"true\">// 25</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Date/getDate\">MDN Documentation</a></p>","fields":{"slug":"/javascript/standard-objects/date/date-prototype-getdate/"},"frontmatter":{"title":"Date.prototype.getDate"}}},"pathContext":{"slug":"/javascript/standard-objects/date/date-prototype-getdate/"}}

